import React, { useState } from 'react'
import ProjectSdata from './Sdata/ProjectSdata'
import { BrowserRouter as Router, NavLink, Link } from "react-router-dom";

export const Projects = () => {
    const [items, setItems] = useState(ProjectSdata)

    return (
        <>
            <div className="container-fluid poppinfont">


                <div>
                    <div class="card text-white bg-primary mb-3 mx-auto m-5" style={{ maxWidth: '58rem' }}>
                        <div class="card-header fs-5"><i class="fa fa-github-square" aria-hidden="true"></i> My Projects</div>

                        {
                            items.map((elem) => {
                                const { thumb, projectname, technologies, projectlevel, link } = elem;
                                console.log(elem)

                                return (
                                    <div class="card-body">
                                        <div className="ms-1 card-header p-3 ">
                                            <div>
                                                <img src={thumb} alt="" className='img-fluid img-thumbnail' style={{ maxWidth: '40vh' }} />
                                            </div>
                                            <div className="ms-3">
                                                <h4 class="card-text mt-3"><i class="fa fa-globe" aria-hidden="true"></i> {projectname}</h4>
                                                <p className='card-text'><i class="fa fa-microchip" aria-hidden="true"></i> {technologies}</p>
                                                <p className='card-text'><i class="fa fa-level-up" aria-hidden="true"></i> Complexity Level : {projectlevel}</p>
                                                <p className='card-text'><i class="fa fa-tags" aria-hidden="true"></i> Compelete Project Developed By Me</p>

                                                <div className='d-flex '>

                                                    <a href={link}
                                                        className="btn btn-warning">
                                                        Source Code
                                                    </a>

                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                )
                            })
                        }
                    </div>


                </div>
            </div>
        </>

    )
}
