import React, { useState } from 'react'
import next from '../images/cib-next-js.svg'
import LanguageSdata from './Sdata/LanguageSdata'

export const Languageworkedon = () => {
    const [items, setItems] = useState(LanguageSdata)
    return (
        <>

            <div className="container-fluid poppinfont">
                <div class="card text-white bg-primary mb-3 mx-auto m-5" style={{ maxWidth: '58rem' }}>
                    <div class="card-header fs-5"> <i class="fa fa-code" aria-hidden="true"></i> Languages i have worked on</div>

                    {
                        items.map((elem) => {
                            const { icon, name } = elem;
                            console.log(elem)

                            return (
                                <div class="card-body">
                                    <div className="ms-5 card-header p-3">
                                        <p class="card-text"> {name}</p>

                                    </div>




                                </div>
                            )
                        })
                    }
                </div>
            </div>


        </>
    )
}
