import React, { useState } from 'react'
import Sdata from './Sdata/Sdata'

export const Education = () => {
    const [items, setItems] = useState(Sdata);
    return (
        <>
            <div className="container-fluid poppinfont">
                <div className="mx-auto text-center mt-3">
                    <h3>Requested Datails</h3>
                </div>

                <div>
                    <div class="card text-white bg-primary mb-3 mx-auto m-5" style={{ maxWidth: '58rem' }}>
                    <div class="card-header fs-5"><i class="fa fa-book" aria-hidden="true"></i> Education Details such as college, branch, pass out year </div>

                        {
                            items.map((elem) => {
                                const { query1, degree, stream,university, year } = elem;
                                console.log(elem)

                                return (
                                    <>
                                       
                                        <div class="card-body">
                                            <div className="ms-5 card-header p-3">
                                                <p class="card-text"><i class="fa fa-graduation-cap" aria-hidden="true"></i> {degree}</p>
                                                <p class="card-text"><i class="fas fa-stream    "></i> {stream}</p>
                                                <p className='card-text'><i class="fa fa-university" aria-hidden="true"></i> {university}</p>
                                                <p className='card-text'><i class="fa fa-calendar-o" aria-hidden="true"></i> {year}</p>
                                            </div>

                                        </div>
                                    </>
                                )
                            })
                        }



                    </div>

                </div>
            </div>
        </>
    )
}
