import React from 'react'

export const Salary = () => {
    return (
        <>
             <div className="container-fluid poppinfont">
            <div class="card text-white bg-primary mb-3 mx-auto m-5" style={{maxWidth: '58rem'}}>
                <div class="card-header fs-5"> <i class="fas fa-accessible-icon"></i> My Salary Expectation</div>
                <div class="card-body">
                    <div className="ms-5 card-header p-3">
                    <p class="card-text"><i class="fa fa-dot-circle-o" aria-hidden="true"></i> What I’m really looking for is something in the range of 4LPA to 4.5LPA. I believe that’s the range this company pays for my level of employment, and it’s a range I’m comfortable with.</p> 
                    </div>
                </div>

                <div class="card-body">
                    <div className="ms-5 card-header p-3">
                    <p class="card-text"><i class="fa fa-dot-circle-o" aria-hidden="true"></i> I want to join your company to work on a long term basis. For that I need to be paid remuneration commensurate with my qualification, experience and equal to the average salary in the industry for such jobs.If I am paid this salary, I will be motivated to perform to the best of my ability.</p> 
                    </div>
                </div>
            </div>
            </div>
        </>
    )
}
